@import url(https://fonts.googleapis.com/css2?family=Nerko+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
.dogpic {
    margin: auto;
    width: 280px;
    height: 280px;
    background-image: url(/static/media/landingpagedog.ed47dab3.png);
    background-size: cover;
}
.landingpage-div h1 {
    text-align: center;
    margin: 15px;
    border-bottom: solid 2px black;
}
.landingpage-div p {
    text-align: center;
    margin: 10px;
    font-size: 19px;
    line-height: 30px;
}
.getStarted-btn {
    text-align: center;
    margin-top: 20px;
}

.btn {
    border: 5px solid #2c3e50;
    color: #2c3e50;
    font-size: 16px;
    letter-spacing: 0.1rem;
    padding: 1rem;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    background-color: #1abc9c;
    box-shadow: 10px 10px 0 #3498db, 20px 20px 0 #e26a6a;
    cursor: pointer;
    font-weight: bold;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.btn:hover {
    box-shadow: none;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

@media all and (min-width: 800px) {
    .dogpic {
        width: 330px;
        height: 330px;
    }
    .catpic {
        background-image: url(/static/media/landingpagecats.db98e777.jpg);
        width: 330px;
        height: 330px;
        background-size: cover;
        background-position: -60px 10px;
    }
    .landingpics {
        margin: auto;
        display: grid;
        grid-template-columns: auto auto;
        margin-bottom: 50px;
        margin-top: 50px;
    }
    .landingpage-div p {
        font-size: 22px;
        margin: auto;
        margin-top: 20px;
        margin-bottom: 45px;
        width: 600px;
    }
    .btn {
        font-size: 20px;
    }
}

.each-petInfo h2 {
    margin: 10px;
}
.pet-image {
    text-align: center;
}
.pet-image img {
    width: 330px;
    margin-bottom: 20px;
}
.petInfo {
    margin-bottom: 40px;
}
.petInfo p {
    margin-left: 10px;
    margin-bottom: 5px;
}

.control-center {
    margin-bottom: 30px;
    text-align: center;
}

.control-center button {
    background-color: #1abc9c;
    border: 2px solid;
    font: inherit;
    line-height: 1;
    margin: 10px;
    padding: 10px 20px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.control-center button:hover {
    box-shadow: 5px 5px rgb(255, 255, 103), 10px 10px rgb(255, 125, 125);
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.newname-form label {
    font-size: 20px;
    margin-right: 10px;
}
.newname-form input {
    height: 25px;
    width: 180px;
}
.newname-form input:focus {
    outline: none;
}
.newname-form button {
    padding: 7px 15px;
}
.adoption-page h2 {
    margin: 10px;
}
.adopter-name {
    text-align: center;
    margin: 20px;
    font-family: "Nerko One", cursive;
    font-size: 30px;
}

.header {
    text-align: center;
    margin: 20px;
    padding: 10px;
    color: white;
    font-size: 25px;
    background-color: #6666ff;
}

* {
    padding: 0;
    margin: 0;
}
body {
    font-family: "Righteous", cursive;
}

